import React, { Component } from 'react';
import logo from '../../assets/images/logo.png';
import linkedin from '../../assets/images/linkedin.png';
import linkedinW from '../../assets/images/linkedinW.png';
import githubFilled from '../../assets/images/githubFilled.png';
import githubW from '../../assets/images/githubW.png';
import resume from '../../assets/images/resume.png';
import resumeW from '../../assets/images/resumeW.png';
import resumeLink from '../../assets/files/rrtong_resume.pdf';

class Header extends Component {
  render() {
    return (
      <header>
        <div className='logo'>
          <a href='https://rrtong.info/'><img className={'navImg'} src={logo} alt='logo'></img></a>
        </div>

        <nav className='nav'>
          <ul>
            <li className='first'>
              <a href='https://www.linkedin.com/in/rrtong' target='_blank' rel='noopener noreferrer'><img className={'navImg'} src={linkedinW} alt='linkedin' onMouseOver={e => (e.currentTarget.src = linkedin)} onMouseOut={e => (e.currentTarget.src = linkedinW)}></img></a>
            </li>
            <li>
              <a href='https://github.com/rrtong' target='_blank' rel='noopener noreferrer'><img className={'navImg'} src={githubW} alt='github' onMouseOver={e => (e.currentTarget.src = githubFilled)} onMouseOut={e => (e.currentTarget.src = githubW)}></img></a>
            </li>
            <li className='last'>
              <a href={resumeLink} target='_blank' rel='noopener noreferrer'><img className={'navImg'} src={resumeW} alt='resume' onMouseOver={e => (e.currentTarget.src = resume)} onMouseOut={e => (e.currentTarget.src = resumeW)}></img></a>
            </li>
          </ul>
        </nav>
      </header>
    );
  }
}

export default Header;